.page {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__dial {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: absolute;
}

.stopwatch__center-circle {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  z-index: 0;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  top: 20vmin;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  top: 2vmin;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

